@mixin mobile {
    @media screen and (max-width: 480px) {
      @content;
    }
  }
  @mixin tablet {
    @media screen and (max-width: 768px) {
      @content;
    }
  }
  @mixin laptop {
    @media screen and (max-width: 1200px) {
      @content;
    }
  }
.formContainer {
    background-color: rgb(34, 34, 34);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;

    .formWrapper {
        background-color: rgb(209, 66, 90);
        padding: 20px 60px;
        border-radius: 10px;
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-items: center;

        .logo {
            color: WHITE;
            font-weight: bold;
            font-size: 24px;
        }

        .title {
            color: white;
            font-size: 12px;
        }

        form {
            display: flex;
            flex-direction: column;
            gap: 15px;

            input {
                padding: 15px;
                border: none;
                border-bottom: 1px solid darkgray;
                background-color: rgb(209, 66, 90);
                width: 250px;

                &::placeholder {
                    color: darkgray;
                }

                outline: none;
            }

            button {
                background-color: white;
                color: rgb(209, 66, 90);
                padding: 10px;
                font-weight: bold;
                border: none;
                cursor: pointer;
            }

            label {
                display: flex;
                align-items: center;
                gap: 10px;
                color: white;
                font-size: 12px;
                cursor: pointer;

                img {
                    width: 32px;
                }

            }

        }

        p {
            color: darkgray;
            font-size: 12px;
            margin-top: 10px;
        }
    }
}

.home {
    background-color: rgb(34, 34, 34);
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;

    .container {
        border: 1px solid darkgray;
        border-radius: 10px;
        width: 65%;
        height: 80%;
        display: flex;
        overflow: hidden;
        @include tablet {
            width: 90%;
           }
        .sidebar {
            flex: 1;
            border-right: 1px solid darkgray;
            background-color: darkgray;

            .navbar {
                display: flex;
                align-items: center;
                background-color: rgb(42, 40, 40);
                height: 50px;
                padding: 10px;
                justify-content: space-between;
                color: gray;

                .logo {
                    font-weight: bold;
                    color: white;
                    @include tablet {
                        display: none;
                      }
                }

                .user {
                    display: flex;
                    gap: 10px;

                    img {
                        background-color: gray;
                        height: 24px;
                        width: 24px;
                        border-radius: 90%;
                        object-fit: cover;
                    }

                    button {
                        background-color: rgb(79, 74, 74);
                        color: gray;
                        font-size: 10px;
                        border: none;
                        cursor: pointer;
                        @include tablet {
                            position: absolute;
                            bottom: 10px;
                          }

                    }
                }
            }

            .search {
                border-bottom: 1px solid gray;

                .searchForm {
                    padding: 10px;

                    input {
                        background-color: transparent;
                        border: none;
                        color: rgb(78, 76, 76);
                        outline: none;

                        &::placeholder {
                            color: black;
                        }
                    }
                }
            }

            .userChat {
                padding: 10px;
                display: flex;
                align-items: center;
                gap: 10px;
                color: rgb(78, 76, 76);
                cursor: pointer;

                &:hover {
                    background-color: rgb(118, 117, 117);
                }

                img {
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    object-fit: cover;
                }

                .userChatInfo {
                    span {
                        font-size: 18px;
                        font-weight: bold;
                    }

                    p {
                        font-size: 14px;
                        color: black;
                    }
                }
            }
        }

        .chat {
            flex: 2;

            .chatInfo {
                height: 50px;
                font-weight: bold;
                background-color: gray;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 10px;
                color: black;
            }

            .chatIcons {
                display: flex;
                gap: 10px;

                img {
                    height: 24px;
                    cursor: pointer;
                }
            }

            .messages {
                background-color: rgb(209, 66, 90);
                padding: 10px;
                height: calc(100% - 160px);
                overflow: scroll;

                .message {
                    display: flex;
                    gap: 20px;

                    .messageInfo {
                        display: flex;
                        flex-direction: column;
                        color: gray;
                        font-weight: 300;
                        margin-bottom: 20px;

                        img {
                            height: 20px;
                            width: 20px;
                            border-radius: 50%;
                            object-fit: cover;

                        }
                    }

                    .messageContent {
                        max-width: 80%;
                        display: flex;
                        flex-direction: column;
                        gap: 10px;

                        p {
                            background-color: white;
                            padding: 10px 20px;
                            border-radius: 0 10% 10% 10%;
                            max-width: max-content;
                        }

                        img {
                            width: 50%;
                        }
                    }

                    &.owner {

                        flex-direction: row-reverse;
                        .messageContent {
                            align-items: flex-end;

                            p {
                                background-color: gray;
                                color: white;
                                border-radius: 10px 0 10px 10px;
                            }
                        }
                    }
                }
            }

            .input {
                height: 50px;
                background-color: white;
                padding: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                input {
                    width: 100%;
                    border: none;
                    outline: none;
                    color: rgb(78, 76, 76);
                    font-size: 18px;

                    &::placeholder {
                        color: lightgray;
                    }
                }

                .send {
                    display: flex;
                    align-items: center;
                    gap: 10px;

                    img {
                        height: 24px;
                        cursor: pointer;
                    }

                    button {
                        border: none;
                        padding: 10px 15px;
                        color: white;
                        background-color: rgb(209, 66, 90);
                    }
                }
            }
        }
    }

}